import React, {useEffect, useState} from 'react';
import {Link} from "react-router-dom";
import axios from "axios";
import {API_PATH_RU} from "../../tools/constants";

const MainNews = () => {


    const [news, setNews] = useState([])

    useEffect(() => {
        axios.get(API_PATH_RU + "index/v1/news-list/latest/")
            .then(res => {
                setNews(res.data)
            })
            .catch(err =>{
                console.log(err)
            })
    } , [])
    return (
        <div className="main-news" id="news">
            <div className="container">
                <div className="news-main-title">

                    <h2 className="font-family-medium">
                        
                    </h2>
                    <Link to="/all-news"> </Link>
                </div>
                <div className="row">





                    {
                        news.map(item =>(


                            <div className="col-md-4">
                                <img src={item.get_img_url} alt=""/>

                                <h3 className="font-family-medium">{item.title}</h3>
                                <p>{item.content}<Link  to={"/all-news/news-detail/" + item.id}> </Link></p>
                            </div>


                        ))
                    }


                    {/*<div className="col-md-4">*/}
                    {/*    <img src="/images/e3.png" alt=""/>*/}

                    {/*    <h3 className="font-family-medium">CityNet     IT</h3>*/}
                    {/*    <p> CityNet       IT-,  */}
                    {/*               . <Link href="#!"> </Link></p>*/}
                    {/*</div>*/}


                    {/*<div className="col-md-4">*/}
                    {/*    <img src="/images/e2.png" alt=""/>*/}

                    {/*    <h3 className="font-family-medium">C     Promobot V4</h3>*/}
                    {/*    <p> CityNet       Promobot V4. <Link href="#!"> </Link></p>*/}
                    {/*</div>*/}


                    {/*<div className="col-md-4">*/}
                    {/*    <img src="/images/e1.png" alt=""/>*/}

                    {/*    <h3 className="font-family-medium">     ()</h3>*/}
                    {/*    <p>           */}
                    {/*        ,        <Link href="#!"> </Link></p>*/}
                    {/*</div>*/}


                </div>
            </div>
        </div>
    );
};

export default MainNews;